.main__btn {
  background-color: $primary-color;
  border-radius: 24px;
  color: #fff;
  padding: 0.4em 1em;
  transition: 0.3s;
}

.second__btn {
  background-color: #fff;
  border-radius: 24px;
  border: 1px solid #00000063;
  padding: 0.4em 1em;
  color: #00000063;
  transition: 0.3s;
}

.head__title {
  font-size: $medium-font;
  margin: 1em 0 2em 0;
  h3 {
    display: inline-block;
    position: relative;
    &:after {
      position: absolute;
      content: "";
      top: 50%;
      left: 105%;
      transform: translateY(-50%);
      background-color: $primary-color;
      height: 1px;
      width: 50%;
    }
  }
}
