header {
  .top__header {
    border-bottom: 1px solid $black-light;
    .wrapper {
      @include full-flexer;
      justify-content: space-between;
    }

    .comp__info,
    .comp__social {
      ul {
        @include flex-menu;
        height: 100%;
        li {
          border-left: 1px solid $black-light;
          a {
            @include flexer(center, center);
            color: $black;
            text-decoration: none;
            font-size: $small-font;
            padding: 0.8em 1em;
            svg {
              width: 16px;
              height: 16px;
              margin-right: 0.5em;
            }
          }
          &:last-child {
            border-right: 1px solid $black-light;
          }
        }
      }
    }
  }
  .lower__header {
    position: relative;
    padding: 0.4em 0;
    .logo {
      svg {
        width: 180px;
      }
    }
    .site__menu {
      height: 100%;
      .desk__menu__row {
        @include flexer(flex-end, center);
        height: 100%;
        .desk__menu__item {
          padding: 0 2em;
          position: relative;
          @include xxl {
            padding: 0 1.4em;
          }
          a {
            text-decoration: none;
            color: $black;
            font-size: $medium-font;
            text-transform: uppercase;
          }
          span {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            cursor: pointer;
          }
          .sub__menu__item {
            bottom: 0;
            left: 0;
            ul {
              padding: 0;
              margin: 0;
              list-style: none;
            }
          }
        }
      }
    }
    .site__search {
      @include flexer(flex-end, center);
      height: 100%;
      #srch {
        cursor: pointer;
      }
      .search__bar {
        padding: 0;
        width: 0;
        height: 100%;
        position: absolute;
        background-color: $primary-color;
        right: 0;
        top: 0;
        overflow: hidden;
        @include flexer(space-between, center);
        transition: 0.5s;
        form {
          display: flex;
          align-items: center;
          padding: 1em;
          border-radius: 4px;
          background-color: $secondary-color2;
          flex-grow: 2;
          width: 100%;
          button {
            background: transparent;
            border: 0;
            &:focus {
              outline: 0;
            }
            svg {
              height: 1.4em;
              width: 1.4em;
              circle,
              line {
                stroke: #fff;
              }
            }
          }
          input {
            flex-grow: 2;
            background: transparent;
            border: 0;
            color: #fff;
            &:focus {
              outline: 0;
            }
            &::placeholder {
              color: #fff;
              font-size: $large-font;
            }
          }
        }
        button {
          border: 0;
          background-color: transparent;
          margin-left: 0.5em;
          svg {
            fill: #fff;
            width: 30px;
          }
        }
      }
      .search__bar.show {
        width: 100%;
        padding: 0 5%;
      }
    }
  }
}
